import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {timer} from 'rxjs';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {SharedServiceService} from '../../shared-service.service';
import {Location} from '@angular/common';
import {ApiService} from '../../api.service';
import {AlertController} from '@ionic/angular';
import * as moment from "moment";
import {StorageService} from '../../shared-components/storage.service';
import {appConstants} from '../../../assets/constants/app-constants';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.page.html',
  styleUrls: ['./signup.page.scss'],
})
export class SignupPage implements OnInit {
  loginForm: FormGroup;
  handlerMessage: string;
  constructor(private storageService: StorageService, private adminService: ApiService, private alertController: AlertController, private location: Location, private sharedService: SharedServiceService, private router: Router, private formBuilder: FormBuilder) { }
  ngOnInit() {
    this.loginForm = this.formBuilder.group({
      name : new FormControl({value: '', disabled: false}, [
        Validators.required
      ]),
      dob: new FormControl({value: '01/01/1993', disabled: false}, [
        Validators.required
      ]),
      email: new FormControl({value: '', disabled: false}, [
        Validators.required
      ])
    });
  }

  async onSignUp() {
    if (this.loginForm.invalid) {
      await this.sharedService.presentToast('Please fill all the mandatory fields', 'error');
      return;
    }
    const data = {
      name: this.loginForm.get('name').value,
      email: this.loginForm.get('email').value,
      dateOfBirth: moment(this.loginForm.get('dob').value).format('L'),
    };
    // const userId = this.sharedService.getUserId();
    const userInfo: any = await this.storageService.getStoredValue(appConstants.USER_INFO);
    this.adminService.updateUser(data, userInfo.id).subscribe(
        res => this.updateUserSuccess(res),
        error => {
          this.adminService.commonError(error);
          this.sharedService.showSpinner.next(false);
        }
    );
  }

  updateUserSuccess(res) {
    this.sharedService.presentToast('User information updated!', 'success');
    this.router.navigate(['/feed']);
  }

  async reasonAlert() {
    const alert = await this.alertController.create({
      header: 'Do you want to re-submit your phone number?',
      cssClass: 'alert-popup',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            this.storageService.removeStoredItem(appConstants.REFRESH_TOKEN_KEY);
            this.storageService.removeStoredItem(appConstants.ACCESS_TOKEN_KEY);
            this.storageService.removeStoredItem(appConstants.USER_INFO);
          },
        },
        {
          text: 'OK',
          role: 'confirm',
          handler: () => {
            this.sharedService.removeAllUserDetails();
            this.router.navigate(['login']);
            this.handlerMessage = 'Alert confirmed';
          },
        },
      ]
    });

    await alert.present();
  }

  onBackButton() {
    this.reasonAlert();
    // this.location.back();
  }
}
